import uCharts from '@/components/u-charts/u-charts.js'
var scaleLine = null
export default {
	data() {
		return {
			title: '报表',
			cWidth: '',
			cHeight: '',
			pixelRation: 1
		};
	},
	onLoad() {
		this.cWidth = uni.upx2px(750)
		this.cHeight = uni.upx2px(400)
		this.getDayData()
	},
	onShow() {
		
	},
	methods: {
		showSaleLine(canvasId, chartData){
			scaleLine = new uCharts({
				canvasId: canvasId,
				type: 'line',
		    colors:['#facc14', '#f04864', '#8543e0', '#90ed7d'],
				fontSize:11,
				padding:[15,15,0,15],
				legend:{
					show:true,
					padding:5,
					lineHeight:11,
					margin:0,
				},
				dataLabel:false,
				dataPointShape:true,
				background:'#FFFFFF',
				pixelRatio:this.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				xAxis: {
					type:'grid',
					gridColor:'#CCCCCC',
					gridType:'dash',
					dashLength:8
				},
				yAxis: {
					gridType:'dash',
					gridColor:'#CCCCCC',
					dashLength:8,
				},
				width:this.cWidth * this.pixelRation,
				height: this.cHeight * this.pixelRation,
				extra: {
					line:{
						type: 'straight'
					}
				}
			})
		},
		getDayData() {
			let saleData={categories:[], series:[]}
			saleData.categories = ['01:00', '02:00', '03:00', '04:00', '05:00', '06:00']
			saleData.series= [
				{
					name: '销售额',
					data: [100, 130, 150, 130, 120, 80],
					color: '#facc14'
				},
				{
					name: '利润',
					data: [50, 80, 100, 80, 50, 30],
					color: '#2fc25b'
				}
			]
			this.showSaleLine("saleLine",saleData)
		}
	},
	touchSaleLine(e) {
		saleLine.showToolTip(e, {
			format: function (item, category) {
				return category + ' ' + item.name + ':' + item.data 
			}
		});
	}
}
